import React, { useState } from "react";
import { useCart } from "react-use-cart";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";
import "../Cart/Cart.css";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";

export default function Cart({ data }) {
  const { title, colour, location } = data;
  const temp = location !== undefined ? location : "/";

  const [open, setOpen] = useState(false);
  const onOpenModal = () => setOpen(true);
  const onCloseModal = () => setOpen(false);

  const notify = () => toast.success("Order placed!");

  const {
    isEmpty,
    totalItems,
    items,
    cartTotal,
    emptyCart,
    updateItemQuantity,
    removeItem,
  } = useCart();
  console.log(items);

  const closeIcon = (
    <button type="button" className="nes-btn">
      <i className="nes-icon close is-small"></i>
    </button>
  );

  return (
    <>
      <div>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <Link
            className="nes-btn is-primary"
            to={`${temp}`}
            style={{
              paddingTop: 10,
              paddingBottom: 10,
              width: "10%",
              height: "5%",
            }}
          >
            {"< Back"}
          </Link>
          <h1 style={{ paddingTop: 10, paddingBottom: 10 }} className={colour}>
            {title}
          </h1>
          <button
            className="nes-btn is-primary"
            type="button"
            onClick={onOpenModal}
            style={{
              paddingTop: 10,
              paddingBottom: 10,
              width: "10%",
              height: "5%",
            }}
          >
            {/* <img
              alt="Cart"
              src="data:image/png;base64,"
            ></img> */}
            <text
              style={{
                fontSize: 18,
              }}
            >
              Cart ({totalItems})
            </text>
          </button>
        </div>

        <Modal
          open={open}
          onClose={onCloseModal}
          center
          closeIcon={closeIcon}
          classNames={{
            overlay: "customOverlay",
            modal: "customModal",
          }}
        >
          <h3>Cart ({totalItems})</h3>
          {isEmpty ? (
            <p>Your cart is empty</p>
          ) : (
            <div
              style={{
                paddingTop: 20,
              }}
            >
              <table>
                <thead>
                  <th>
                    <h5>Item </h5>
                  </th>
                  <th>
                    <h5>Size </h5>
                  </th>
                  <th>
                    <h5>Price each</h5>
                  </th>
                  <th>
                    <h5>Quantity</h5>
                  </th>
                </thead>
                <tbody>
                  {items.map((item) => (
                    <tr key={item.id}>
                      <td> {item.title} </td>
                      <td> {item.options[0].option} </td>
                      <td> {"R" + item.price} </td>

                      <td>
                        <button
                          className="nes-btn is-primary"
                          type="button"
                          onClick={() =>
                            updateItemQuantity(item.id, item.quantity - 1)
                          }
                        >
                          -
                        </button>
                        {" " + item.quantity + " "}
                        <button
                          className="nes-btn is-primary"
                          type="button"
                          onClick={() =>
                            updateItemQuantity(item.id, item.quantity + 1)
                          }
                        >
                          +
                        </button>
                        <button
                          className="nes-btn is-error"
                          type="button"
                          onClick={() => removeItem(item.id)}
                        >
                          &times;
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>

              <button
                className="nes-btn is-success checkOut"
                type="button"
                onClick={() => {
                  notify();
                  emptyCart();
                  onCloseModal();
                }}
              >
                <text className="checkOutText">Checkout</text>
              </button>
              <h3 className="total">Total R{cartTotal.toFixed(2)}</h3>
              <Toaster />
            </div>
          )}
        </Modal>
      </div>
    </>
  );
}

Cart.propTypes = {
  data: PropTypes.object,
};
